.pagelib_footer_menu_item {
  padding: 10px;
  background-size: 40px 40px;
  background-repeat: no-repeat;
  background-color: white;
  margin-top: 0.5px;
  margin-bottom: 0.5px;
}

.pagelib_theme_dark .pagelib_footer_menu_item {
  background-color: #2e3136;
}

.pagelib_theme_sepia .pagelib_footer_menu_item {
  background-color: #f0e6d6;
}

.pagelib_theme_black .pagelib_footer_menu_item {
  background-color: #222;
}

html[dir='ltr'] .pagelib_footer_menu_item {
  background-position: left center;
}

html[dir='rtl'] .pagelib_footer_menu_item {
  background-position: right center;
}

.pagelib_footer_menu_item_title {
  color: black;
  min-height: 1.38em;
}

.pagelib_theme_dark .pagelib_footer_menu_item_title,
.pagelib_theme_black .pagelib_footer_menu_item_title {
  color: #f8f9fa;
}

html[dir='ltr'] .pagelib_footer_menu_item_title {
  padding-left: 50px;
}

html[dir='rtl'] .pagelib_footer_menu_item_title {
  padding-right: 50px;
}

.pagelib_footer_menu_item_subtitle {
  color: #a2a9b1;
  font-size: 0.9em;
  line-height: 1.1;
}

.pagelib_theme_sepia .pagelib_footer_menu_item_subtitle {
  color: #7c776e;
}

html[dir='ltr'] .pagelib_footer_menu_item_subtitle {
  padding-left: 50px;
}

html[dir='rtl'] .pagelib_footer_menu_item_subtitle {
  padding-right: 50px;
}

