.pagelib_edit_section_header {
  display: flex;
  align-items: flex-end;
}

.pagelib_edit_section_title {
  flex: 1;
}

#pagelib_edit_section_title_pronunciation {
  background-image: url("data:image/png;base64,");
  background-repeat: no-repeat;
  background-position: center;
  opacity: .54;
  margin: auto 0.35em 0.1em 0.35em;
  display: inline-block;
  background-size: contain;
  width: 0.8em;
  height: 0.8em;
  vertical-align: middle;
}

html[dir="rtl"] #pagelib_edit_section_title_pronunciation {
  -webkit-transform: scaleX( -1 );
  transform: scaleX( -1 );
}

#mainpage a.pagelib_edit_section_link,
.no-editing a.pagelib_edit_section_link {
  display: none;
}

.pagelib_edit_section_link_container {
  margin: 0px 0px 0px 16px;
  opacity: .54;
}

h2 + .pagelib_edit_section_link_container {
	margin-bottom: 8px;
}

.pagelib_theme_dark .pagelib_edit_section_link_container,
.pagelib_theme_black .pagelib_edit_section_link_container,
.pagelib_theme_dark #pagelib_edit_section_title_pronunciation,
.pagelib_theme_black #pagelib_edit_section_title_pronunciation {
  opacity: .7;
  -webkit-filter: invert(100%);
  filter: invert(100%);
}

.pagelib_theme_dark.pagelib_compatibility_filter .pagelib_edit_section_link_container,
.pagelib_theme_black.pagelib_compatibility_filter .pagelib_edit_section_link_container {
  -webkit-filter: none;
  filter: none;
}

.pagelib_theme_sepia .pagelib_edit_section_link_container,
.pagelib_theme_sepia #pagelib_edit_section_title_pronunciation {
  opacity: .7;
}

.content-rtl .pagelib_edit_section_link_container {
  -webkit-transform: scaleX( -1 );
  transform: scaleX( -1 );
  margin: 0px 16px 0px 0px;
}

a.pagelib_edit_section_link {
  position: relative;
  width: 24px;
  height: 24px;
  display: inline-block;
  background-size: 24px 24px;
  background-repeat: no-repeat;

  background-image: url(data:image/svg+xml;base64,);
}

.pagelib_theme_dark.pagelib_compatibility_filter a.pagelib_edit_section_link,
.pagelib_theme_black.pagelib_compatibility_filter a.pagelib_edit_section_link {
  background-image: url('data:image/svg+xml;base64,');
}

.page-protected a.pagelib_edit_section_link {
  background-image: url(data:image/svg+xml;base64,);
}

.pagelib_theme_dark.pagelib_compatibility_filter.page-protected a.pagelib_edit_section_link,
.pagelib_theme_black.pagelib_compatibility_filter.page-protected a.pagelib_edit_section_link {
  background-image: url('data:image/svg+xml;base64,');
}

.pagelib_platform_android a.pagelib_edit_section_link:active {
  background-color: transparent;
}

.pagelib_platform_android a.pagelib_edit_section_link:before {
  content: '';
  position: absolute;
  z-index: -1;
  border-radius: 50%;
   -webkit-transform: scale( 0 );
  transform: scale( 0 );
  -webkit-transition: transform 300ms cubic-bezier( 0.25, 0.46, 0.45, 0.94 );
  transition: transform 300ms cubic-bezier( 0.25, 0.46, 0.45, 0.94 ); /* ease out quad */
}

.pagelib_platform_android a.pagelib_edit_section_link:active:before {
  width: 200%;
  height: 200%;
  top: -50%;
  left: -50%;
  background-color: #eee;
  -webkit-transform: scale( 0.6 );
  transform: scale( 0.6 );
}

.pagelib_platform_android.pagelib_theme_dark.pagelib_compatibility_filter a.pagelib_edit_section_link:active:before,
.pagelib_platform_android.pagelib_theme_black.pagelib_compatibility_filter a.pagelib_edit_section_link:active:before {
  background-color: #111;
}

.pagelib_platform_ios h3.pagelib_edit_section_title + span.pagelib_edit_section_link_container,
.pagelib_platform_ios h4.pagelib_edit_section_title + span.pagelib_edit_section_link_container,
.pagelib_platform_ios h5.pagelib_edit_section_title + span.pagelib_edit_section_link_container,
.pagelib_platform_ios h6.pagelib_edit_section_title + span.pagelib_edit_section_link_container {
    display: none;
}

/* Wikidata description styling */
#pagelib_edit_section_title_description {
    font-size: 14px;
    line-height: 22px;
    color: #777D84;
    margin-top: 3px;
    margin-bottom: 5px;
    padding-bottom: 0px;
}

#pagelib_edit_section_add_title_description {
    font-size: 14px;
    font-style: italic;
    display: inline;
}

#pagelib_edit_section_add_title_description:before {
    content: "";
    background-image: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAACAAAAAMCAYAAAADFL+5AAAAAXNSR0IArs4c6QAAADFJREFUOBFjNE47859hAAHTANoNtnrUAQMdA6P2D3wIMFK7HDg7y4SRFG+NZsMBDwEAJj8Gc0QaQK8AAAAASUVORK5CYII=');
    background-repeat: no-repeat;
    width: 1.8em;
    height: 0.75em;
    background-size: 1.0em 0.375em;
    display: inline-block;
    background-position: left center;
}

html[dir="rtl"] #pagelib_edit_section_add_title_description:before {
    -webkit-transform: scaleX( -1 );
    transform: scaleX(-1);
}

/* Horizontal divider between title/description and lead section text */
#pagelib_edit_section_divider {
    border: 1px solid #ccc;
    margin: 12px 0px 16px 0px;
    width: 60px;
}

@media (-webkit-min-device-pixel-ratio: 2) {
    #pagelib_edit_section_divider {
        border-width: 0.5px;
    }
}
